import React, { Fragment } from 'react'
import { Menu } from '../components/Menu'
import { Project } from '../components/Project'

import iconHtml from '../icons/html.svg'
import iconCss from '../icons/css.svg'
import iconSass from '../icons/sass.svg'
import iconJs from '../icons/js.svg'
import iconJQuery from '../icons/jquery.svg'
import iconReactJs from '../icons/react.svg'

import '../css/Portfolio.css'

export const Portfolio = ({ mode , actMode }) => {
  return (
    <Fragment>
      <div className={mode ? 'page page-dark' : 'page page-light'}>
        <div className='container container-portfolio'>
          <h2 className='subtitle'>Mis proyectos</h2>
          <Project
            name='Reloj analogico y digital'
            description="Un reloj analogico y uno digital teniendo ambos el modo 'dark' o su modo 'light'"
            tecnologias={[iconHtml,iconCss,iconJs,iconReactJs]}
            web='https://hoyosjuan16.github.io/watch-view/'
            repositorio='https://github.com/HoyosJuan16/watch' 
            image='https://i.ibb.co/2KLRzLb/2020-10-21-22h41-55.png'
          />
          <Project
            name='Juego de la memoria'
            description='El juego consiste en encontrar la pareja de cada cartas que estan boca abajo, la dificultad puede ser elegida o puede ser al azar.'
            tecnologias={[iconHtml,iconCss,iconSass,iconJs,iconJQuery]}
            web='https://hoyosjuan16.github.io/busca-pareja/'
            repositorio='https://github.com/HoyosJuan16/busca-pareja' 
            image='https://i.ibb.co/vL08Kvh/proyecto-memoria.png'
          />
          <Project
            name='Blog Viajes'
            description='Una plantilla basica para un blog. Lo que llama la atencion de este blog es su banner y la forma en la que cambia el fondo de pantalla.'
            tecnologias={[iconHtml,iconCss,iconSass,iconJs]}
            web='https://hoyosjuan16.github.io/BlogViajes/'
            repositorio='https://github.com/HoyosJuan16/BlogViajes' 
            image='https://i.ibb.co/99WV61N/proyecto-blog-viajes.png'
          />
          <Project
            name='Tienda Cursos'
            description='Una practica de una tienda orientada a cursos, pero obviamente aplicable a cualquier tipo de productos.'
            tecnologias={[iconHtml,iconCss,iconSass,iconJs]}
            web='https://hoyosjuan16.github.io/tienda-cursos/'
            repositorio='https://github.com/HoyosJuan16/tienda-cursos' 
            image='https://i.ibb.co/VHfN5dJ/proyecto-tienda-cursos.png'
          />
          <Project
            name='Lista de tareas'
            description='Una lista de tareas organizable.'
            tecnologias={[iconHtml,iconCss,iconSass,iconJs]}
            web='https://hoyosjuan16.github.io/lista-de-tareas/'
            repositorio='https://github.com/HoyosJuan16/lista-de-tareas/' 
            image='https://i.ibb.co/sR8M0Pt/2020-10-22-09h18-52.png'
          />
          <Project
            name='Search Movies'
            description='Una app que busca peliculas y obtiene los resultados a traves de una api.'
            tecnologias={[iconHtml,iconCss,iconJs,iconReactJs]}
            web='/'
            repositorio='https://github.com/HoyosJuan16/search-movies/' 
            image='https://i.ibb.co/WznPWsn/2020-10-21-23h12-38.png'
          />
          {/* <Project
            name='E-commerce Electrodomesticos'
            description='Una plantilla basica para una ecommerce. Me inspire en la pagina de mercado libre y la de una tienda llamada Fravega.'
            tecnologias={[iconHtml,iconCss,iconSass,iconJs]}
            web='https://hoyosjuan.github.io/E-commerce/'
            repositorio='https://github.com/HoyosJuan/E-commerce' 
            image='https://i.ibb.co/nDZVzBJ/proyecto-ecommerce.png'
          /> */}
        </div>
      </div>
      <Menu 
        selected='3'
        mode={mode}
        actMode={actMode}
      />
    </Fragment>
  )
}